<template xmlns:v-lazy="http://www.w3.org/1999/xhtml">
    <div class="dynamic">
        <!-- navigate -->
        <mu-appbar class="appbar navigator" :style="{zIndex: open ? 8000 : 1}">
            <mu-button slot="left" color="#fff" @click="$router.back()" icon>
                <mu-icon value="navigate_before"/>
            </mu-button>
            <mu-button class="w" color="#fff" @click="handlerToggle" flat>
                <span>{{types.active}}</span>
                <mu-icon value="arrow_drop_down"/>
            </mu-button>
            <mu-button slot="right" color="#fff" @click="$router.push('/dynamic/type')" icon>
                <mu-icon value="add"/>
            </mu-button>
        </mu-appbar>
        <!-- navigate -->

        <!-- list -->
        <div class="scroll-wrapper" ref="container">
            <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
                <div class="list-item paper" v-for="(item, index) in list" :key="item.id"
                     @click="handlerItem(index, item)">
                    <div class="source">
                        <mu-avatar size="50">
                            <img v-lazy="AvatarImage($images + item.portrait, item.alias)"
                                 :key="$images + item.portrait">
                        </mu-avatar>
                        <div class="info">
                            <div class="nickname">{{item.alias}}</div>
                            <div class="time theme-text-secondary">{{item.createTime}}</div>
                        </div>
                    </div>
                    <div class="msg" v-html="item.content">{{item.content}}</div>
                    <div class="picture-box" v-if="item.images" ref="images">
                        <div class="picture border-1px" v-for="(image, index) in item.images" v-if="image"
                             :class="handlerPicture(item.images)"
                             v-lazy:background-image.container="image"
                             @click.stop="onClickPicture(item.images, index)"></div>
                    </div>
                    <div class="interact">
                        <div class="interact-item">
                            <mu-icon size="16" value="thumb_up" color="#3899D7"></mu-icon>
                            <span>{{!item.fnum ? '0' : item.fnum }}</span>
                        </div>
                        <div class="interact-item last">
                            <mu-icon size="16" value="sms" color="#3899D7"></mu-icon>
                            <span>{{!item.cnum ? '0' : item.cnum }}</span>
                        </div>
                    </div>
                </div>
                <!-- no success-->
                <no-success text="请输入检索内容" height="90vh" v-if="!list.length"/>
                <!-- no success-->
            </mu-load-more>
        </div>
        <!-- list -->

        <!-- router -->
        <transition name="views-left">
            <keep-alive>
                <router-view class="views" @refresh="refresh"/>
            </keep-alive>
        </transition>
        <!-- router -->

        <!-- mu-popover -->
        <mu-popover class="dynamic-popover" :open.sync="open">
            <div class="list">
                <div class="item border-1px-b"
                     :class="{'color-primary': types.active === item}"
                     v-for="item in types.list"
                     @click="handlerToggleList(item)"
                     :key="item">
                    {{item}}
                </div>
            </div>
        </mu-popover>
        <!-- mu-popover -->
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import Shelter from '../../base/shelter'
    import ImagePreview from 'vant/lib/image-preview'
    import 'vant/lib/image-preview/style'
    import {
        queryCircleMessageList,
        queryCircleMessageListBeFabulousByUserId,
        queryCircleMessageListBeCommentByUserId
    } from "../../api/dynamic"
    import NoSuccess from "../../base/no-success/no-success"
    import {isArray} from "../../types/index"
    import {userMixins} from "../../common/mixins"

    export default {
        name: "dynamicList",
        mixins: [userMixins],
        data() {
            return {
                open: false,
                refreshing: false,
                loading: false,
                params: {
                    pageNumber: 0,
                    pageSize: 15
                },
                list: [],
                types: {
                    active: '全部',
                    list: ["全部", "已赞", "已留言"]
                }
            }
        },
        activated() {
            this.refresh()
        },
        methods: {
            // 刷新
            refresh() {
                this.refreshing = true
                this.$refs.container.scrollTop = 0

                this.getList()
            },
            // 点击选项
            handlerItem(index, item) {
                this.$router.push({
                    name: 'dynamic-detail',
                    params: {
                        id: item.id,
                        portrait: item.portrait,
                        alias: item.alias,
                        content: item.content,
                        images: item.images,
                        isFabulous: item.ifab,
                        createTime: item.createTime,
                        index
                    }
                })
            },
            // 切换类型
            handlerToggle() {
                this.open = true
            },
            // 切换列表
            handlerToggleList(type) {
                if (this.types.active !== type) {
                    this.types.active = type
                    this.getList()
                }

                this.open = false
            },
            // 格式化图片样式
            handlerPicture(images) {
                return images.length > 4 ? 'picture-max' : 'picture-' + images.length
            },
            // 点击图片，弹出图片查看器
            onClickPicture(images, index) {
                ImagePreview({
                    images: images,
                    startPosition: index
                })
            },
            // 加载更多
            load() {
                this.loading = true

                this.params.pageNumber++

                if (this.types.active === '全部') {
                    queryCircleMessageList(this.params).then(res => {
                        this.list.push(...isArray(res).map(item => {
                            if (item.mediaPath) {
                                item.images = item.mediaPath.split(';').map(item => {
                                    return this.$images + item
                                })
                            }

                            return item
                        }))

                        this.loading = false
                    })
                }
                if (this.types.active === '已赞') {
                    queryCircleMessageListBeFabulousByUserId(this.params).then(res => {
                        this.list.push(...isArray(res).map(item => {
                            if (item.mediaPath) {
                                item.images = item.mediaPath.split(';').map(item => {
                                    return this.$images + item
                                })
                            }
                            return item
                        }))

                        this.loading = false
                    })
                }
                if (this.types.active === '已留言') {
                    queryCircleMessageListBeCommentByUserId(this.params).then(res => {
                        this.list.push(...isArray(res).map(item => {
                            if (item.mediaPath) {
                                item.images = item.mediaPath.split(';').map(item => {
                                    return this.$images + item
                                })
                            }
                            return item
                        }))

                        this.loading = false
                    })
                }
            },
            // 获取列表
            getList() {
                if (this.types.active === '全部') {
                    this.queryCircleMessageList()
                }
                if (this.types.active === '已赞') {
                    this.queryMyFabulousList()
                }
                if (this.types.active === '已留言') {
                    this.queryMyCommentList()
                }
            },
            queryCircleMessageList() {
                this.params.pageNumber = 0

                queryCircleMessageList(this.params).then(res => {
                    this.list = res

                    // 格式化图片列表
                    this.list.map(item => {
                        if (item.mediaPath) {
                            item.images = item.mediaPath.split(';').map(item => {
                                return this.$images + item
                            })
                        }
                        return item
                    })

                    this.loading = false
                    this.refreshing = false
                })
            },
            queryMyFabulousList() {
                this.params.pageNumber = 0

                queryCircleMessageListBeFabulousByUserId(this.params).then(res => {
                    this.list = isArray(res).map(item => {
                        if (item.mediaPath) {
                            item.images = item.mediaPath.split(';').map(item => {
                                return this.$images + item
                            })
                        }
                        return item
                    })

                    this.refreshing = false
                })
            },
            queryMyCommentList() {
                this.params.pageNumber = 0

                queryCircleMessageListBeCommentByUserId(this.params).then(res => {
                    this.list = res.map(item => {
                        if (item.mediaPath) {
                            item.images = item.mediaPath.split(';').map(item => {
                                return this.$images + item
                            })
                        }
                        return item
                    })

                    this.refreshing = false
                })
            }
        },
        watch: {
            open(value) {
                if (value) {
                    Shelter.show({
                        onClose: () => {
                            this.open = false
                        },
                        element: this.$el
                    })
                } else {
                    Shelter.close()
                }
            }
        },
        components: {
            NoSuccess,
            Navigate
        }
    };
</script>


<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl';

    .dynamic
        padding-top 46px
        background-color #EFEFEF
        overflow hidden

        .appbar
            position absolute
            top 0
            left 0
            z-index 1
            width 100%
            height 46px
            box-shadow none
            background-color transparent

            >>> .mu-appbar-title
                display flex
                align-items center
                justify-content center
                height 100%

        .scroll-wrapper
            width 100%
            height 100%

            .list-item
                position relative
                padding 15px
                margin-bottom 15px

                .source
                    display flex
                    align-items center
                    margin-bottom 10px

                    > .mu-avatar
                        margin-right 15px

                    .info
                        .nickname
                            margin-bottom 5px
                            font-size $font-size-ls

                        .time
                            font-size $font-size-ms

                .msg
                    margin-bottom 10px
                    line-height 1.74625
                    text-align justify

                .picture-box
                    display flex
                    flex-wrap wrap
                    margin-bottom 10px

                    .picture
                        height 0
                        background-position center center
                        background-repeat no-repeat
                        background-size cover

                    .picture-1
                        padding-top 60%
                        width 60%

                    .picture-2
                        margin-right 4px
                        padding-top calc(50% - 2px)
                        width calc(50% - 2px)

                        &:last-child
                            margin-right 0

                    .picture-3
                        margin-right 3px
                        padding-top calc(33% - 2px)
                        width calc(33% - 2px)

                        &:last-child
                            margin-right 0

                    .picture-4
                        margin-right 4px
                        margin-bottom 4px
                        padding-top calc(50% - 2px)
                        width calc(50% - 2px)

                        &:nth-child(2n)
                            margin-right 0

                        &:nth-child(n + 2)
                            margin-bottom 0

                    .picture-max
                        margin-right 3px
                        margin-bottom 3px
                        padding-top calc(33% - 2px)
                        width calc(33% - 2px)

                        &:nth-child(3n)
                            margin-right 0

                        &:nth-child(n + 7)
                            margin-bottom 0

                .interact
                    display flex
                    justify-content flex-end
                    align-items center
                    height 30px

                    .interact-item
                        display flex
                        align-items center
                        margin-right 15px
                        font-size $font-size-ml

                        > i
                            margin-right 5px

                        &.last
                            margin-right 0

    .dynamic-popover
        position absolute
        top 46px
        left 0
        right 0
        width 100%
        max-width 100%
        max-height 50vh
        box-shadow none

        .list
            .item
                padding 15px
                text-align center
</style>



